<template>
    <div class="fixedbg" ref="fixedbg"
         :style="{backgroundImage : 'url(' + imageUrl() + ')', backgroundSize:imagesize, backgroundPosition:imageposition}">
        <div class="mask-fixedbg"></div>
    </div>
</template>
<style scoped>
    /*������дcss��ʽ*/
    .fixedbg {
        position: fixed;
        z-index: -2;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #000;
    }

    .fixedbg:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #000;
        opacity: 0;
        visibility: hidden;
        transition: all 0.5s;
    }

    .mask-fixedbg {
        position: fixed;
        z-index: 111111111111;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(0, 0, 0, 0.4);
    }
</style>
<script type="text/javascript-6"> 
// ����д�߼�����
export default {

    data () {
        return {
            imagesize: 'cover',
            imageposition: 'center center'
        }
    },
    created() {
       this.$http.get('https://cn.bing.com/HPImageArchive.aspx?format=js&idx=0&n=1').then(response => {
            // get body data
            localStorage.setItem("imageUrlPart",response.body.images[0].url)
        }, response => {
            console.log("error");
        });
    },
    methods: {
        imageUrl(){
            return 'http://www.bing.com' +localStorage.getItem("imageUrlPart")
        }
    }
}





</script>


