<template>
    <div class="label">
        <div @click="back()">
            <i class="back"></i>
        </div>
        <h1>是的客观合理的收费管理和电视里发挥各类活动上了覆盖</h1>
    </div>
</template>
<style scoped>
    /*在这里写css样式*/
    .back {
        display: inline-block;
        width: 40px;
        height: 40px;
        background: url("https://raw.githubusercontent.com/vvvvvvvvvvip/new/master/image/back.png") center center no-repeat;
        background-size: 100%;
        position: absolute;
        left: 30px;
    }

    .middle-content {
        position: fixed;
        top: 125px;
        left: 50%;
        -webkit-transform: translateX(-50%);
        -moz-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
        -o-transform: translateX(-50%);
        transform: translateX(-50%);
    }

    .label {
        width: 100%;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: 83px;
    }
</style>
<script type="text/javascript-6"> 
// 这里写逻辑代码
export default {
    methods: {
        back: function(){
            this.$router.go(-1)
        }
    }
}
</script>


