<template>
    <div class="works">
        <div @click="back()">
            <i class="back"></i>
        </div>
        <ul class="middle-content">
            <h2>页面布局展示</h2>
            <li class="center" v-for="(item,index) in workInfo" :key="item.id">
                <span :title="item.workName">{{ item.workName }}</span>
                <div class="linkBox">
                    <a target="item.address" :href="item.address">演示地址</a>
                    <a target="item.projectAddress" :href="item.projectAddress">项目地址</a>
                </div>
            </li>
            <h2>jquery系列</h2>
            <li class="center" v-for="(item,index) in jquery" :key="item.id">
                <span :title="item.workName">{{ item.workName }}</span>
                <div class="linkBox">
                    <a target="item.address" :href="item.address">演示地址</a>
                    <a target="item.projectAddress" :href="item.projectAddress">项目地址</a>
                </div>
            </li>
            <h2>animation</h2>
            <li class="center" v-for="(item,index) in animation" :key="item.id">
                <span :title="item.workName">{{ item.workName }}</span>
                <div class="linkBox">
                    <a target="item.address" :href="item.address">演示地址</a>
                    <a target="item.projectAddress" :href="item.projectAddress">项目地址</a>
                </div>
            </li>
            <h2>vue系列</h2>
            <li class="center" v-for="(item,index) in vue" :key="item.id">
                <span :title="item.workName">{{ item.workName }}</span>
                <div class="linkBox">
                    <a target="item.address" :href="item.address">演示地址</a>
                    <a target="item.projectAddress" :href="item.projectAddress">项目地址</a>
                </div>
            </li>
        </ul>
    </div>
</template>
<style scoped>
    /*在这里写css样式*/
    a {
        color: #fff;
        list-style: none;
        text-decoration: none;
        font-weight: lighter;
        font-size: 14px;
    }

    ul li {
        width: 100%;
        line-height: 40px;
        text-align: left;
        font-weight: 200;
        font-size: medium;
        color: #f0f0f0;
        display: flex;
        margin: 0;
    }

    ul li span {
        -webkit-box-flex: 1;
        -ms-flex: 0 1 auto;
        flex: 0 1 auto;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        width: 12%;
        font-size: 14px;
    }

    h2 {
        color: #fff;
        text-align: left;
    }

    .center {
        text-align: center;
    }

    .linkBox {
        -ms-flex: 1 1 auto;
        flex: 1 1 auto;
        text-align: right;
    }

    .back {
        display: inline-block;
        width: 40px;
        height: 40px;
        background: url("https://raw.githubusercontent.com/vvvvvvvvvvip/new/master/image/back.png") center center no-repeat;
        background-size: 100%;
        position: absolute;
        left: 30px;
    }

    .middle-content {
        position: fixed;
        top: 45px;
        left: 50%;
        -webkit-transform: translateX(-50%);
        -moz-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
        -o-transform: translateX(-50%);
        transform: translateX(-50%);
        width: 80%;
    }

    .works {
        width: 100%;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: 83px;
    }

    @media screen and (max-width: 768px) {
        ul li span {
            width: 64px;
        }
    }
</style>
<script type="text/javascript-6"> 
// 这里写逻辑代码
export default {
    data (){
        return {
            workInfo: [],
            jquery: [],
            vue: [],
            animation: []
        }
    },
    methods: {
        back: function(){
            this.$router.go(-1)
        }
    },
    created(){
        this.$http.get('../../../../static/data.json').then((response) => {
            console.log(response.data.workList.page)
            this.workInfo = response.data.workList.page
            this.jquery = response.data.workList.jquery
            this.animation = response.data.workList.animation
            this.vue = response.data.workList.vue
        }, response => {
            console.log("error");
        });
    }
}

</script>


