<template>
    <div id="app">
        <fixed-bg></fixed-bg>
        <v-content :info="info"></v-content>
        <router-view></router-view>
    </div>
</template>

<script type="text/javascript-6">
    import store from './vuex/index';
    import fixedbg from './components/common/fixedbg.vue'
    import homeComponent from './components/home.vue'
    import content from './components/common/content.vue'
    export default {
    name: 'App',
        components: {
            'fixed-bg': fixedbg,
            homeComponent,
            'v-content': content
        },
  data () {
   return {
       info: {},
    }
  },
  created() {
   this.$http.get('https://cn.bing.com/cnhp/coverstory/').then(response => {
         console.log(response)
         // get body data
         this.info = response.data

         // 所有的bing数据存起来
         store.dispatch('set_BingInfo', response.data)

         localStorage.setItem('fixedImageBg', JSON.stringify(response.data))
        }, response => {
            console.log("error");
        });
  }
}
</script>

<style>
    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
    }
    body {
        margin: 0;
    }
    h1, h2 {
        font-weight: normal;
    }
    ul {
        list-style-type: none;
        padding: 0;
    }
    li {
        display: inline-block;
        margin: 0 10px;
    }
    a {
        color: #42b983;
    }
    .container {
        padding: 0 10%;
    }
    header {
        position: fixed;
        top: 0;
        left: 50%;
        -webkit-transform: translateX(-50%);
        -moz-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
        -o-transform: translateX(-50%);
        transform: translateX(-50%);
        width: 100%;
    }
    footer {
        position: fixed;
        bottom: 0;
        left: 50%;
        -webkit-transform: translateX(-50%);
        -moz-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
        -o-transform: translateX(-50%);
        transform: translateX(-50%);
        width: 100%;
    }
    @media screen and (max-width: 768px) {
        .fullScreen {
            display: none;
        }
    }


</style>



